body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(89deg, #ff5edf 0%, #005593 100%);
  transition: all 1s ease;
}

button {cursor:pointer; transition: all 1s ease;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1, p { }

.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #00ff00 inset;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap:wrap;
	        flex-wrap:wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor:pointer;
}
.Tilt {
	background: linear-gradient(0deg, #ff5edf 0%, #04c8de 100%);
}
.form {
	width:700px;
	background-image: repeating-linear-gradient(45deg, transparent, transparent 35px, rgba(255,255,255,.5) 35px, rgba(255,255,255,.5) 50px);
}
button:hover {background-color: #9368c0;}
.App {
  text-align: center;
}


.center {
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center

}
.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left:0;
  right:0;
  z-index: -1;
}
